<template>
    <form class="row">
      <div class="form-group col-sm-6">
        <label for="named">名称</label>
        <input type="text" class="form-control" id="named" placeholder="名称" v-model="named">
        <small id="namedTip" class="form-text text-muted">{{ tip_named }}</small>
      </div>
      <div class="form-group col-sm-6">
        <label for="num">编号</label>
        <input type="text" class="form-control" id="num" placeholder="编号" v-model="num">
        <small id="numTip" class="form-text text-muted">{{ tip_num }}</small>
      </div>
      <div class="form-group col-sm-12">
        <label for="addr">地址</label>
        <input type="text" class="form-control" id="addr" placeholder="地址" v-model="addr">
        <small id="addrTip" class="form-text text-muted">{{ tip_addr }}</small>
      </div>
      <div class="col-sm-12 mt-2">
        <h5 class="overflow-hidden">联络人</h5>
      </div>
      <div class="form-group col-sm-6">
        <label for="contact_named">名称</label>
        <input type="text" class="form-control" id="contact_named" placeholder="名称" v-model="contact_named">
        <small id="contact_namedTip" class="form-text text-muted">{{ tip_contact_named }}</small>
      </div>
      <div class="form-group col-sm-6">
        <label for="contact_area">电话</label>
        <div class="row">
          <div class="col-sm-4">
            <select class="form-control" id="contact_area" v-model="contact_area">
              <option v-for="a in areas" :key="a.id" :value="a.area">{{ a.area }}</option>
            </select>
          </div>
          <div class="col-sm-8">
            <input type="text" class="form-control" id="contact_phoned" placeholder="电话" v-model="contact_phoned">
            <small id="contact_phonedTip" class="form-text text-muted">{{ tip_contact_phoned }}</small>
          </div>
        </div>
      </div>
    </form>
</template>

<script>
import view from '@/common/es/view'
import tool from '@/common/es/tool'
import validate from '@/common/es/validate'

export default {
  name: 'MemberPlusForm',
  data() {
    return {
      areas: [
        {
          id: 1,
          area: '+ 852'
        },
        {
          id: 2,
          area: '+ 853'
        },
        {
          id: 3,
          area: '+ 86'
        },
      ],

      named: '',
      num: '',
      addr: '',
      contact_named: '',
      contact_phoned: '',
      contact_area: '+ 852',


      tip_named: '',
      tip_num: '',
      tip_addr: '',
      tip_contact_named: '',
      tip_contact_phoned: '',
    }
  },
  methods: {
    submitMemberPlusForm() {
      const validate = this.validateForm()
      console.log('提交')
    },
    // 
    tipClose(block) {
      setTimeout(() => {
        if (block == 'num') { this.tip_num = '' }
        else if (block == 'named') { this.tip_named = '' }
        else if (block == 'addr') { this.tip_addr = '' }
        else if (block == 'contact_named') { this.tip_contact_named = '' }
        else if (block == 'contact_phoned') { this.tip_contact_phoned = '' }
      }, 4200)
    },
    tipForm(res, block) {
      let show = ''
      if (res != true) {
        show = res
      }
      // 
      if (block == 'num') { this.tip_num = show }
      else if (block == 'named') { this.tip_named = show }
      else if (block == 'addr') { this.tip_addr = show }
      else if (block == 'contact_named') { this.tip_contact_named = show }
      else if (block == 'contact_phoned') { this.tip_contact_phoned = show }

      this.tipClose(block)
    },
    validateForm() {
      let res = validate.val_empty(this.num, '货号')
      if (res == true) {
        res = validate.val_char(this.num, '货号')
        this.tipForm(res, 'num')
      } else { this.tipForm(res, 'num') }

      res = validate.val_empty(this.named, '名称')
      if (res == true) {
        res = validate.val_char(this.named, '名称')
        this.tipForm(res, 'named')
      } else { this.tipForm(res, 'named') }

      res = validate.val_empty(this.addr, '地址')
      if (res == true) {
        res = validate.val_char(this.addr, '地址')
        this.tipForm(res, 'addr')
      } else { this.tipForm(res, 'addr') }

      res = validate.val_empty(this.contact_named, '联络人名称')
      if (res == true) {
        res = validate.val_char(this.contact_named, '联络人名称')
        this.tipForm(res, 'contact_named')
      } else { this.tipForm(res, 'contact_named') }

      res = validate.val_empty(this.contact_phoned, '联络人电话')
      if (res == true) {
        res = validate.val_char(this.contact_phoned, '联络人电话')
        this.tipForm(res, 'contact_phoned')
      } else { this.tipForm(res, 'contact_phoned') }

      return res
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


